import React,{ useState } from "react";

import Button from "../UI/Button";
import Card from '../UI/Card';
import classes from './AddEmployee.module.css';

const AddEmployee =(props)=>{


    const [empcode , setEmpcode ] = useState('');
    const [entername, setEnterName] = useState('');
    const [enterdepartmet , setEnterDepartmet] = useState('');
    const [entergender, setEnterGender] = useState('');
    const [enterdob, setEnterDob] = useState('');
    const [enterjoiningdate , setEnterJoiningdate] = useState('');
    const [enterprevexperience, setEnterPrevexperience] = useState('');
    const [entersalary , setEnterSalary] = useState('');
    const [enteraddress , setEnterAddress] = useState('');

    const addemplyee =(event)=>{
        event.preventDefault();

        props.onAddEmplyee(empcode, entername , enterdepartmet ,entergender , enterdob , enterjoiningdate , enterprevexperience , entersalary , enteraddress);
        setEmpcode('');
        setEnterName('');
        setEnterDepartmet('');
        setEnterGender('');
        setEnterDob('');
        setEnterJoiningdate('');
        setEnterPrevexperience('');
        setEnterSalary('');
        setEnterAddress('');

    }

     const empcodeHandlar =(event)=>{
         setEmpcode(event.target.value)
     }

     const nameHandlar =(event)=>{
         setEnterName(event.target.value);
     }

     const departmetHandlar =(event)=>{
         setEnterDepartmet(event.target.value);
     }

     const genderHandlar =(event)=>{
        setEnterGender(event.target.value);
     }

     const dobHandlar =(event)=>{
        setEnterDob(event.target.value);
     }

     const joiningHandlar =(event)=>{
        setEnterJoiningdate(event.target.value);   
     }

     const experienceHandlar =(event) =>{
        setEnterPrevexperience(event.target.value);  
     }

     const salaryHandlar =(event)=>{
        setEnterSalary(event.target.value);
     }

     const addressHandlar =(event)=>{
        setEnterAddress(event.target.value);
     }

    //  useEffect(() => {
    //     localStorage.setItem("EmpDetails", JSON.stringify(formIsValid))
    // }, [formIsValid]);

    return (
        <Card className={classes.input}>
         <form onSubmit={addemplyee}>
             <h1>Add Employee</h1>
        <label htmlFor="Empcode">Emp Code</label>
        <input
          id="Empcode"
          type="text"
          required
          value={empcode}
          onChange={empcodeHandlar}
        />
        <label htmlFor="name">Name</label>
        <input
          id="name"
          type="text"
          required
          value={entername}
          onChange={nameHandlar}
        />
         <label htmlFor="departmet">Departmet</label>
         <select onChange={departmetHandlar} value={enterdepartmet}>
             <option ></option>
             <option >Admin</option>
             <option >Technology</option>
             <option >Accounts</option>
        </select>
        <label htmlFor="gender">Gender</label>
        <p>Male</p>
        <input 
            id="gender"
            type="radio"
            name="gender"
            value="male"
            onChange={genderHandlar}
        /> 
        <p>Female</p>
        <input 
        id="gender"
        type="radio"
        value={entergender}
        onChange={genderHandlar}
    />
         <label htmlFor="dob">DOB</label>
         <input 
            id="dob"
            type="text"
            required
            value={enterdob}
            onChange={dobHandlar}
        /> 
         <label htmlFor="joiningdate">Joining Date</label>
         <input
          id="joiningdate"
          type="text"
          required
          value={enterjoiningdate}
          onChange={joiningHandlar}
        />
         <label htmlFor="prevexperience">Prev Experience</label>
         <input
          id="prevexperience"
          type="number"
          required
          value={enterprevexperience}
          onChange={experienceHandlar}
        />
         <label htmlFor="salary">Salary</label>
         <input
          id="salary"
          type="number"
          required
          value={entersalary}
          onChange={salaryHandlar}
        />
        <label htmlFor="address">Address</label>
         <input
          id="address"
          type="text"
          required
          value={enteraddress}
          onChange={addressHandlar}
        />
        <Button type="submit">Submit</Button>   <Button type="cancel">Cancel</Button>
      </form>
        </Card>

    )
}

export default AddEmployee;