import React, { useState } from 'react';

import EmployeeList from './component/EmployeeList';
import AddEmployee from './component/AddEmployee';

function App() {

  const [empList, setEmpList] = useState([]);

  const addEmployeeHandler = (empcode, name , departmet , gender ,dob , joiningdate , prevexperience , salary , address) => {
    setEmpList((prevEmployeeList) => {
      return [
        ...prevEmployeeList,
        { empcode: empcode, name: name ,departmet:departmet ,gender:gender , dob:dob ,joiningdate:joiningdate , prevexperience:prevexperience, salary:salary , address:address}
      ];
    });
  };

  return (
    <div className="App">
      <AddEmployee onAddEmplyee={addEmployeeHandler}/>
      <EmployeeList employees={empList}/>
    </div>
  );
}

export default App;
