import React from 'react';

import Card from '../UI/Card';
import classes from './EmployeeList.module.css';

const EmployeeList = (props) => {
  return (
    <Card className={classes.employees}>
      <h1>Details</h1>
        <nav>
          EmpCode || Name || Departmet || Gender  || DOB  ||  JoiningDate  || Prev Exp  || Salary  || Address 
      <ul>
         {props.employees.map((employee) => (
          <li key={employee.id}>
            {employee.empcode} {employee.name} {employee.departmet} {employee.gender} {employee.dob} {employee.joiningdate} {employee.prevexperience} {employee.salary} {employee.address}
          </li>
        ))}
      </ul>
      </nav>
    </Card>
  );
};

export default EmployeeList;
